import React, { useState } from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import images from "../images";
import Modal from "./modal";
import { PiCaretCircleDoubleRightFill } from "react-icons/pi";

const ServicesTiles = () => {
  const { t } = useTranslation();
  const services = t("services", { returnObjects: true });

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedService, setSelectedService] = useState<{
    title: string | null;
    copy: string | null;
    document: string | null;
    image: string | null;
  }>({
    title: null,
    copy: null,
    document: null,
    image: null,
  });

  const handleModalOpen = (service: string) => {
    setSelectedService({
      title: t(`services.${service}.title`),
      copy: t(`services.${service}.copy`),
      document: t(`services.${service}.document`),
      image: t(`services.${service}.modalImage`),
    });
    setIsModalOpen(true);
  };

  const handleModalClose = () => {
    setIsModalOpen(false);
    setSelectedService({
      title: null,
      copy: null,
      document: null,
      image: null,
    });
  };

  return (
    <div className={styles.wrapper}>
      {Object.keys(services).map((service, index) => {
        const iconKey = t(`services.${service}.icon`);
        const image = images[iconKey];

        return (
          <div key={index} className={styles.tile}>
            {image && (
              <img
                src={image.src}
                alt={
                  image.alt || t(`services.${service}.title`) || "Default alt"
                }
                className={styles.icon}
              />
            )}
            <h3 className={styles.title}>{t(`services.${service}.title`)}</h3>
            <p>{t(`services.${service}.intro`)}</p>
            <a onClick={() => handleModalOpen(service)}><PiCaretCircleDoubleRightFill className="text-2xl text-light" /></a>
          </div>
        );
      })}

      <Modal
        isOpen={isModalOpen}
        onClose={handleModalClose}
        title={selectedService.title || ""}
      >
        <div className="flex ">
          <div className="w-1/2">
            {selectedService.image && (
              <img
                src={images[selectedService.image]?.src}
                alt={
                  images[selectedService.image]?.alt || "Default modal image"
                }
              />
            )}
          </div>
          <div className="w-1/2 p-8 pl-0">
            {selectedService.title && <h2 className="text-2xl font-bold">{selectedService.title}</h2>}

            <p className="my-8">{selectedService.copy}</p>

            {selectedService.document && images[selectedService.document] && (
              <a
                href={images[selectedService.document].src}
                target="_blank"
                rel="noopener noreferrer"
                className="bg-secondary text-light px-4 py-2 rounded-md"
              >
                {t("seeDocument")}
              </a>
            )}
          </div>
        </div>
      </Modal>
    </div>
  );
};

export default ServicesTiles;

const styles = {
  wrapper:
    "grid grid-cols-1 sm:grid-cols-3 gap-4 items-stretch mx-4 max-w-[1400px] mx-auto",
  tile: "text-center p-4 rounded-lg text-white bg-secondary/[0.8] shadow-lg flex flex-col justify-center items-center",
  title: "font-bold text-lg",
  icon: "w-12",
};
